@font-face {
   font-family: 'Myriad Pro Light';
   src: url('../Fonts/MyriadPro-Light.otf') format('opentype')
 }
.Title-Container{

    margin: 1em;
    display: flex;
    flex-direction: column;
    padding: 1em;
       align-items: center;
       gap: 1em;
       color: #848484;
       font-weight:500;
       font-family: "Myriad Pro Light"; 
       background-color:#f4f4f4;
       border: 1px solid #ded7d7;
      
    }
 .Title-Input{
    width: 44em;
    color: #252323db; 
    border: 1px solid #ded7d7;
    height: 2.2em;
    text-align: left;
    padding-left: 0.5em;
    font-family: 'Myriad Pro Light';
    padding-top: 0.2em;
 }
 .Title-Input::placeholder{
   color: #0080f6  ;
 }
 .Title-Container-Wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    
 }
 .Title-Wrapper-Select{
   width:45em;
   height:2.5em;
    background-color: #fff;
    border: 1px solid #ded7d7;
    color: #252323db;
    font-family: 'Myriad Pro Light';
    padding-top: 0.2em;
 
 }
 .Title-Select-Option{
   font-weight: 200;
   font-family: 'Myriad Pro Light'!important;
 }
 @media only screen and (max-width: 600px) {

.Title-Container{
   width: 70%;
}
.Title-Input{
   width:55vw;
   font-size:0.6em;
}
.Title-Wrapper-Select{
   width:50vw;
   font-size:0.7em;
}
 }