@font-face {
  font-family: 'Myriad Pro Light';
  src: url('../Fonts/MyriadPro-Light.otf') format('opentype')
}
.QA-Wrapper{
    display:flex;
    flex-direction: column;
    gap: 1em;
    padding: 0.2em;
}
.QuestionInput{
    width: 35em;
    color: #252323db; 
    border: 1px solid #ded7d7;
    height: 2.2em;
    text-align: left;
    padding-left: 0.5em;
    font-family: 'Myriad Pro Light';
    align-self: center;
}
.QuestionInput::placeholder {
  color: #0080f6  ;
}

.IconWrapper{display: flex;
    height: 2em;
    padding-bottom: 0.2em;
    margin-left: -1em;
    margin-top: -0.6em;
    gap: 0.6em;
}
.IconWrapper>.qButton{
    background-color:transparent;
    border:none;
    cursor:pointer;

  }
  
  .delIcon:hover {
    color: rgba(0, 0, 255, 0.679) !important;
  }
  .qButton:hover{
    background-color: rgba(0, 0, 255, 0.159);
  }
  @media only screen and (max-width: 600px) {
    .QuestionInput{
      width:100%;
      font-size:0.7em;
    }
  }
  