@font-face {
    font-family: 'Myriad Pro Light';
    src: url('../Fonts/MyriadPro-Light.otf') format('opentype')
  }

.tableContainer{
padding-top: 1dvw;
width: 100%;
}

.resultsTable {
    border-collapse: collapse;
    width: 100%;
    font-family: "Myriad Pro Light";
    font-weight: 100;
    table-layout: fixed;
}

.resultsTable th,
.resultsTable td {
    padding: 8px;
    font-weight: 100;
    text-align: center;
}

.resultsTable th {
    font-weight: bold;
    font-size: clamp(0.75rem, 0.5833rem + 0.7619vw, 1.25rem);
    color:  #4a50ee;
}

.resultsTable ul {
    list-style-type: none;
    font-size: clamp(0.75rem, 0.5833rem + 0.7619vw, 1.25rem);
    text-align: left;
    padding: 0;
    color:  #4a50ee;
}
.resultsTable ul li {
    color: rgba(0, 0, 0, 0.71);
}

.resultsTable tr:nth-child(even) {
    background-color: #f2f2f2;
}

.resultsTable tr:hover {
    background-color: #ddd;
}

.resultsTable tr {
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    
}

.resultsTable th:not(:last-child),
.resultsTable td:not(:last-child) {
    border-right: 1px solid #ddd;
}
